<template>
  <div class="main-body">
    <div class="carousel">
      <el-carousel :interval="1000" arrow="always" height="120px">
        <el-carousel-item v-for="item in 4" :key="item">
          <h3>{{item}}</h3>
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="goods">
        <div class="good-nav">
          <div class="type"
          v-for="(item, index) in goodList"
          :key="item.type"
          ref="menuList"
          :class="{active: activeType === index}">
            <span @click="selectMenu(index, $event)"> {{item.typeName}}</span>
          </div>
        </div>
        <div class="box" ref="goodsWrapper">
          <div class="good-list">
            <div class="good-module"
            v-for="(item, index) in goodList"
            :key="index"
            ref="goodList">
              <div class="title">{{item.typeName}}:</div>
              <div class="dishes" v-for="dish in item.list" :key="dish.name">
                <div class="img">
                  <img :src="dish.image" alt=""  @click="goodDetail(dish)">
                </div>
                <div class="msg">
                  <div class="name">{{dish.name}}  <span class="tag" v-for="(tag, index) in dish.tag" :key="index">{{tag}}</span></div>
                  <div class="detail">{{dish.detail}}</div>
                  <div class="type">月销量：{{dish.sellCount}}</div>
                  <div class="price">单价：￥{{dish.price}}</div>
                  <div class="count">
                   <number-count :dish="dish"></number-count>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
    </div>
    <transition name="fade">
      <div class="good-detail"  v-if="showDetail" key="showDetail">
        <div class="top">
          <span class="el-icon-arrow-left" @click="showDetail = false"></span>
          <img :src="currentgood.image" alt="">
        </div>
        <div class="evaluate">
          <div class="name">{{currentgood.name}}</div>
          <div class="sale">月售： {{currentgood.sellCount}}</div>
          <div class="price">￥{{currentgood.price}}</div>
          <div class="count">
            <number-count :dish="currentgood"></number-count>
          </div>
        </div>
        <div class="good-describe">
          <p>{{currentgood.detail}}</p>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import numberCount from './inputNumber.vue'
import goods from '../../db.json'
export default {
  name: 'homeMain',
  data () {
    return {
      msg: '商品详情',
      showDetail: false,
      goodList: [
        {
          type: 'potScrap',
          typeName: '锅底',
          list: [
            {
              name: '麻辣',
              price: '38',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/黄豆芽.jpg'
            },
            {
              name: '香辣',
              price: '38',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '清汤',
              price: '38',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            }
          ]
        },
        {
          type: 'veg',
          typeName: '蔬菜类',
          list: [
            {
              name: '土豆',
              price: '3',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '番茄',
              price: '3',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '茄子',
              price: '3',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '金针菇',
              price: '3',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '豆皮',
              price: '3',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '千张',
              price: '3',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '腐竹',
              price: '3',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '菠菜',
              price: '3',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '韭菜',
              price: '3',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '白菜',
              price: '3',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '青菜',
              price: '2',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            }
          ]
        },
        {
          type: 'meat',
          typeName: '肉类',
          list: [
            {
              name: '牛肉',
              price: '33',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '羊肉',
              price: '33',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            },
            {
              name: '兔肉',
              price: '33',
              detail: '入口即化，一口一个嘎嘣脆，简直不要太过分',
              sell: '99',
              count: 0,
              src: './static/img/羊肉卷.jpg'
            }
          ]
        }
      ],
      scrollY: 0,
      listHeight: [],
      orderGoods: [],
      currentgood: {},
      activeIndex: 0
    }
  },
  components: {
    numberCount
  },
  mounted () {
    //  初始化商品列表
    let goodList = goods.goods
    console.log(goodList)
    // this.$nextTick(() => {
    goodList.forEach((item) => {
      item.list.forEach((item2) => {
        item2.count = 0
      })
    })
    this.goodList = goodList
    // })
    // this.$root.eventHub.$emit('change', this.orders)
    this.$nextTick(() => {
      this._initScroll()
      this._calculateHeight()
    })
    this.$root.eventHub.$on('clearCar', (yourData) => {
      // if (1) {
      this.goodList.forEach((item) => {
        item.list.forEach((item2) => {
          item2.count = 0
        })
      })
    })
    this.$root.eventHub.$on('initScroll', (yourData) => {
      this.$nextTick(() => {
        console.log('initsCROLL')
        this._initScroll()
        this._calculateHeight()
      })
    })
    console.log(goods)
  },
  methods: {
    formatter (row, column) {
      return row.price
    },
    // selectType (index) {
    //   this.activeType = index
    // },
    _initScroll () {
      // if (!this.scroll) {
      this.goodsWrapper = new BScroll(this.$refs.goodsWrapper, {
        click: true,
        momentum: true,
        bounce: true,
        probeType: 3
      })
      // } else {
      //   this.goodsWrapper.refresh()
      // };
      this.goodsWrapper.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y))
      })
    },
    selectMenu (index, event) {
      // if (!event._constructed) {
      //   return
      // }
      this.activeIndex = index
      let dom = this.$refs.goodList[index]
      // console.log(dom)
      //  设置滚动时间
      this.goodsWrapper.scrollToElement(dom, 300)
    },
    _calculateHeight () {
      let goodList = this.$refs.goodList
      let height = 0
      this.listHeight.push(height)
      for (let i = 0; i < goodList.length; i++) {
        height += goodList[i].clientHeight
        this.listHeight.push(height)
      }
    },
    goodDetail (dish) {
      console.log(dish)
      this.showDetail = true
      this.currentgood = dish
    }
  },
  computed: {
    activeType () {
      console.log(this.scrollY)
      for (let i = 0; i < this.listHeight.length; i++) {
        if (!(this.listHeight[i + 1]) || (this.scrollY >= this.listHeight[i] && this.scrollY < this.listHeight[i + 1])) {
          return i
        }
      }
    },
    orders () {
      let arr = []
      this.goodList.forEach((item) => {
        let obj = {list: [], type: item.type, typeName: item.typeName}
        item.list.forEach((item2, index, array) => {
          if (item2.count > 0) {
            obj.list.push(item2)
          }
        })
        arr.push(obj)
      })
      return arr
    }
  },
  watch: {
    'orders': {
      handler: function (newVal, oldVal) {
        console.log(newVal)
        let flag = false
        newVal.forEach((item) => {
          if (item.list.length > 0) {
            flag = true
          }
        })
        if (flag) {
          this.$root.eventHub.$emit('goodSelect', newVal)
        }
        // this.$emit('aceptGoods', newVal)
      },
      deep: true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import '../assets/styles/variable';
  .main-body{
    width: 100%;
    display:flex;
    flex-direction:column;
    position: relative;
    .carousel{
      height:120px;
      width: 100%;
      background-color: rgba(7,17,27,0.8);
      .el-carousel__item h3 {
        color: #475669;
        font-size: 18px;
        opacity: 0.75;
        line-height: 120px;
        margin: 0;
      }
      .el-carousel__item:nth-child(2n) {
        background-color: $themeColor;
      }
      .el-carousel__item:nth-child(2n+1) {
        background-color: $themeColor;
      }
    }
    .goods{
      flex:1;
      width:100%;
      overflow:hidden;
      border-top:1px solid #eee;
      background-color: #fff;
      text-align:center;
      display:flex;
      flex-direction:row;
      position:relative;
      .good-nav{
        width:80px;
        height:100%;
        background-color: #fff;
        .type{
          font-size:13px;
          height:40px;
          width:100%;
          text-overflow: ellipsis;
          border-bottom:1px solid #fff;
          box-sizing:border-box;
          line-height:40px;
          span{
            font-size:14px;
          }
        }
        .active{
          color: $themeColor;
        }
      }
      .box{
        width:100%;
        height:auto;
        .good-list{
          flex:1;
          overflow:auto;
          .good-module{
            width:100%;
            min-height:50px;
            .title{
              line-height:40px;
              text-align:left;
              width:100%;
              padding:0 5px;
            }
            .dishes{
              height:80px;
              width:100%;
              overflow:hidden;
              border-bottom:1px solid #eee;
              padding:0 5px;
              box-sizing:border-box;
              text-align:left;
              display:flex;
              flex-direction:row;
              .img{
                height:70px;
                width:70px;
                padding: 5px 0;
                img{
                  height:70px;
                  width:70px;
                }
              }
              .msg{
                flex:1;
                padding: 0 0 0 5px;
                font-size:12px;
                .name{
                  height:20px;
                  line-height:20px;
                  width:100%;
                  text-overflow: ellipsis;
                  .tag{
                    background-color: $themeColor;
                    border-radius:5px;
                    padding:0 2px;
                    margin-left:5px;
                    font-size:10px;
                    color:#fff;
                  }
                }
                .sell{
                  height:20px;
                  line-height:20px;
                  width:100%;
                  text-overflow: ellipsis;
                }
                .detail{
                  height:20px;
                  line-height:20px;
                  width:100%;
                  text-overflow: ellipsis;
                  white-space:no-wrap;
                  overflow:hidden;
                }
                .price{
                  height:20px;
                  line-height:20px;
                  float:left;
                }
                .count{
                  float:right;
                  width:60px;
                  line-height:20px;
                }
              }
            }
          }
        }
      }
    }
    .good-detail{
      position: absolute;
      top:0;
      bottom:0;
      left:0;
      right:0;
      box-sizing:border-box;
      overflow-y:scroll;
      z-index:11;
      border-raduis:4px;
      background-color: rgba(255,255,255,1);
      display:flex;
      flex-direction:column;
      .top{
        border-bottom:1px dashed #eee;
        img{
          width:100%;
        }
        span{
          position:absolute;
          left:10px;top:10px;
          border-radius:50%;
          background-color:rgba(0,0,0,0.2);
          font-size:20px; padding:5px;
        }
      }
      .evaluate{
        padding:10px;
        text-align:left;
        border-bottom:1px solid #eee;
        .name{
          font-size:16px;
          padding:5px 0;
        }
        .sale{
          font-size:12px;
          padding:5px 0;
        }
        .price{
          height:20px;
          padding:5px 0;
          line-height:20px;
          float:left;
          color:$themeColor;
        }
        .count{
          float:right;
          padding:5px 0;
          width:60px;
          line-height:20px;
        }
      }
      .good-describe{
        padding:10px 2px;
        text-align:left;
        text-indent:2em;
      }
    }
  }
</style>



