<template>
   <div class="number-counts">
     <!-- <el-input-number v-model.number="dish.count" @change="handleChange" label="描述文字" size="mini"></el-input-number> -->
     <!-- <input type="number" v-model="dish.count"> -->
     <!-- {{item}} -->
     <transition name="fade">
       <div class="subtract" v-on:click.stop="subtract"  v-show="dish.count !== 0"><i class="el-icon-remove"></i></div>
     </transition>

     <div class="number" v-show="dish.count !== 0"><input type="text" v-model.number="dish.count" style="border:none;width:100%;text-align:center;"></div>
     <div class="add" @click.stop="add"><i class="el-icon-circle-plus"></i></div>
   </div>
</template>

<script>
// import '../assets/iconfont/iconfont.css'
export default {
  name: 'number-count',
  mounted () {
  },
  data () {
    return {
      // orderFoods: []
      item: ''
    }
  },
  props: {
    dish: {
      type: Object
    }
  },
  methods: {
    subtract () {
      if (this.dish.count > 0) {
        this.dish.count -= 1
      } else {
        this.dish.count = 0
      }
    },
    add () {
      console.log('add')
      if (this.dish.count < 100) {
        this.dish.count += 1
      } else {
        this.dish.count = 99
      }
    },
    handleChange (number) {
      console.log(number)
    }
  }
}
</script>

<style lang="scss" scoped>
   @import '../assets/styles/variable';
   .fade-enter{
     opacity:0;
     transform:translateX(20px);
   }
   .fade-enter-active{
     transition:all 1s;
   }
   .fade-leave-active{
     transition:transform 1s;
   }
   .fade-leave-to{
     transform:translateX(20px);
   }

  .number-counts{
    height:20px;
    line-height:20px;
    .subtract{
      float:left;
      width:20px;
      height:20px;
      font-size:20px;
      color:$themeColor;
      border-radius:50%;
      text-align: center;
    }
    .add{
      float:right;
      width:20px;
      height:20px;
      font-size:20px;
      color:$themeColor;
      border-radius:50%;
      text-align: center;
    }
    .number{
      float:left;
      width:20px;
      height:20px;
      text-align: center;
    }
  }
</style>



