<template>
  <div class="main-body">
    <!-- <h4>{{order}}</h4> -->
    <div class="show-foods">
      <div class="header-title">
        购物车
      </div>
    <h3 v-if=" order.length === 0">{{msg}}</h3>
      <el-collapse v-model="activeNames" @change="handleChange" accordion>
        <div class="food" v-for="(item, index) in order" :key="index" ref="order">
          <el-collapse-item :name="index" v-if="item.list.length > 0">
            <template slot="title">
               <span class="type-name">{{item.typeName}}</span>
               <span class="num-radius">{{item.list.length}}</span>
            </template>
            <div class="dish" v-for="dish in item.list" :key="dish.name">
              <div class="img">
                <img :src="dish.image" alt="">
              </div>
              <div class="msg">
                <p class="name">{{dish.name}}</p>
                <div class="count-num">
                  <span class="price">￥{{dish.price}}</span>
                  <number-count :dish="dish"></number-count>
                </div>
              </div>
            </div>
            <div class="no-food" v-if="item.list.length === 0  ">
              尚未点购
            </div>
          </el-collapse-item>
        </div>
      </el-collapse>
      <div class="money" v-if=" order.length !== 0">
        <span>合计:  {{totalMoney}}</span>
        <div class="count-button" @click="submitOrder">提交订单</div>
      </div>
    </div>
  </div>
</template>

<script>
import numberCount from './inputNumber.vue'
export default {
  name: 'orderMain',
  mounted () {
    this.$root.eventHub.$on('goodSelect', (yourData) => {
      if (yourData) {
        console.log(yourData)
        // if (yourData)
        this.order = yourData
        this.exchange()
        this.countMoney()
      }
    })
  },
  data () {
    return {
      activeNames: ['1'],
      msg: '暂无点购商品',
      order: [],
      singleOrder: [],
      totalMoney: 0
    }
  },
  methods: {
    exchange () {
      this.singleOrder = []
      this.singleOrder.push(...this.order)
      let arr = []
      this.singleOrder.forEach((item) => {
        item.list.forEach((item2) => {
          let obj = {}
          obj.name = item2.name
          obj.price = item2.price
          obj.count = item2.count
          // obj.count.push(item2.count)
          obj.image = item2.image
          arr.push(obj)
        })
      })
      this.singleOrder = arr
      console.log(this.singleOrder)
    },
    countMoney () {
      this.totalMoney = 0
      this.order.forEach((item) => {
        item.list.forEach((item2) => {
          let _thisMoney = item2.price * item2.count
          this.totalMoney += _thisMoney
        })
      })
    },
    handleChange (val) {
      console.log(val)
    },
    submitOrder () {
      console.log(this.order)
      let arr = [this.totalMoney, this.singleOrder]
      this.$root.eventHub.$emit('submitOrder', arr)
      this.$nextTick(() => {
        this.order = []
        this.totalMoney = 0
        this.$root.eventHub.$emit('clearCar')
      })
      this.$router.push('order')
    }
  },
  components: {
    numberCount
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import '../assets/styles/variable';
  .main-body{
    width: 100%;
    .show-foods{
      width:100%;
      height:100%;
      box-sizing:border-box;
      border-radius:5px;
      background-color: #fff;
      position:relative;
      display:flex;
      flex-direction:column;
      .header-title{
        height:40px;
        line-height:40px;
        font-size:20px;
        font-weight:900;
        border-bottom:1px solid #ddd;
      }
      h3{
        margin-top:30px;
      }
      .el-collapse{
        flex:1;
        border:none;
        text-indent:1em;
        overflow:auto;
        .food{
          text-align: left;
          padding:0 5px;
          .type-name{
            text-align:left;
            text-indent:0;
            display:inline-block;
            min-width:60px;
          }
          .num-radius{
            display:inline-block;
            line-height:20px;
            background-color:$themeColor;
            width:20px;
            height:20px;
            text-align:center;
            text-indent:0;
            border-radius:50%;
            color:#fff;
            vertical-align:baseline;
            margin-left:10px;
          }
          .dish{
            display:flex;
            flex-direction:row;
            .img{
              height:70px;
              width:70px;
              padding: 5px 0;
              img{
                height:70px;
                width:70px;
              }
            }
            .msg{
              flex:1;
              height:80px;
              padding:2px 5px;
              box-sizing:border-box;
              display:flex;
              flex-direction:column;
              justify-content:space-between;
              .name{
              }
              .price{
              }
              .count-num{
                .number-counts{
                  float:right;
                  width:60px;
                  line-height:20px;
                  text-indent:0em;
                }
              }
            }
          }
          .no-food{
          }
        }
      }
      .money{
        width:100%;
        height:40px;
        //  background-color: $themeColor;
        //  position:absolute;
        //  bottom:0px;
        border-top:1px solid $themeColor;
        line-height:40px;
        .count-button{
          color:#fff;
          position: absolute;
          width:80px;
          height:40px;
          background-color: $themeColor;
          right:0px;
          bottom:0px;
        }
      }
    }
  }
</style>



