<template>
  <div class="main-body">
    <!-- <h3>{{msg}}</h3>
    <h3>{{order}}</h3> -->
    <div class="main">
      <div class="header-title">
          订单
      </div>
      <div class="order">
        <div class="goods" v-for="(order, index) in list" :key="index" :class="{add: index !== 0}">
          <div class="good" v-for="(food, index) in order" :key="index">
            <div class="img float-left">
              <img :src="food.image" alt="">
            </div>
            <div class="msg float-left">
              <div>
                <span class="name float-left">{{food.name}}</span>
                <span class="single-count float-right" >￥{{food.count * food.price}}</span>
              </div>
              <!-- <div class="price">单价：{{food.price}}</div> -->
              <div class="count"> × {{food.count}} </div>
              </div>
          </div>
        </div>
        <div class="count" v-if="list.length === 0">
          暂无
        </div>
      </div>
      <div class="total-count align-right" v-if="list.length !== 0">
        合计：{{totalCount}}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'meMain',
  mounted () {
    this.$root.eventHub.$on('submitOrder', (yourData) => {
      console.log(yourData)
      if (yourData) {
        // console.log(yourData)
        if (yourData[0] > 0) {
          console.log(1)
          this.totalCount += yourData[0]
          this.list.push(yourData[1])
        }
        // this.list.forEach((item))
        // this.excOrder()
      }
    })
  },
  data () {
    return {
      msg: '我的主页',
      order: [],
      list: [],
      totalCount: 0
    }
  },
  methods: {
    // excOrder () {
    //   let arr = []
    //   this.order.forEach((item) => {
    //     // console.log(item)
    //     item.list.forEach((item2) => {
    //       let obj = {}
    //       obj.name = item2.name
    //       obj.price = item2.price
    //       obj.count = item2.count
    //       obj.image = item2.image
    //       arr.push(obj)
    //     })
    //   })
    //   console.log(arr)
    //   // this.list = arr
    // }
  },
  watch: {
    'list': {
      handler: function (newVal, oldVal) {
        console.log(newVal)
        // newVal.forEach((item, index, array) => {
        //   array.forEach((item2, index2, array2) => {
        //     if (item.name === item2.name && index !== index2) {
        //       item.count.push(item2.count[0])
        //       delete array2[index2]
        //     }
        //   })
        // })
      },
      deep: true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import '../assets/styles/variable';
  .main-body{
    width: 100%;
    height: 100%;
    overflow:hidden;
    .main{
      width: 100%;
      height: 100%;
      display:flex;
      flex-direction:column;
      .header-title{
        height:40px;
        line-height:40px;
        font-size:20px;
        font-weight:900;
        border-bottom:1px solid #ddd;
      }
      .order{
        flex:1;
        overflow:auto;
        .goods{
          // border-bottom:1px dashed $themeColor;
          padding:5px;
          margin:5px 0;
          border-top:1px solid #999;
          border-bottom:1px solid #999;
          .good{
            background-color: #F5F5F5;
            font-size:12px;
            margin-bottom: 2px;
            height:60px;
            line-height:30px;
            width:100%;
            display:flex;
            flex-direction:row;
            .img{
              width:70px;
              height:60px;
              display:flex;
              flex-direction:row;
              justify-content:flex-start;
              align-items:center;
              // box-sizing:border-box;
              // padding:5px;
              img{
                width:50px;
                height:50px;
                padding:0 10px;
                vertical-align:middle;
              }
            }
            .msg{
              flex:1;
              padding: 0 10px;
              display:flex;
              flex-direction:column;
              justify-content:flex-start;
              .count{
                text-align:left;
              }
            }
          }
        }
      }
      .total-count{
        height:40px;
        line-height:40px;
        color:$themeColor;
        font-size:20px;
        font-weight:900;
        padding: 0 5px;
        text-align:center;
      }
      .add{
        .good{
          .msg{
            .count{
              color:$themeColor;
            }
          }
        }
      }
    }
  }
</style>



